'use client';

import React, { useState } from 'react';
import PhoneInput from '../../molecules/PhoneInput/PhoneInput';
import EmailInput from '../../molecules/EmailInput/EmailInput';
import styles from './LoginForm.module.scss';

const LoginForm = () => {
  const [loginMethod, setLoginMethod] = useState<'email' | 'mobile'>('email');
  const [email, setEmail] = useState('');
  const [countryCode, setCountryCode] = useState('+90');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [password, setPassword] = useState('');

  return (
    <div className={styles.loginForm}>
      <h2>Login To Rockie</h2>
      <div className={styles.toggleButtons}>
        <button
          className={loginMethod === 'email' ? styles.active : ''}
          onClick={() => setLoginMethod('email')}
        >
          Email
        </button>
        <button
          className={loginMethod === 'mobile' ? styles.active : ''}
          onClick={() => setLoginMethod('mobile')}
        >
          Mobile
        </button>
      </div>

      <div className={styles.inputGroup}>
        {loginMethod === 'email' ? (
          <EmailInput email={email} onEmailChange={setEmail} />
        ) : (
          <PhoneInput
            countryCode={countryCode}
            phoneNumber={phoneNumber}
            onCountryCodeChange={setCountryCode}
            onPhoneNumberChange={setPhoneNumber}
          />
        )}
      </div>

      <input
        type="password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        placeholder="Password"
        className={styles.input}
      />

      <div className={styles.options}>
        <label>
          <input type="checkbox" />
          Remember me
        </label>
        <a href="#">Forgot Password?</a>
      </div>

      <button className={styles.loginButton}>Login</button>

      <p className={styles.registerPrompt}>
        Not a Member? <a href="#">Register</a>
      </p>
    </div>
  );
};

export default LoginForm;
